h1,h2,h3,p,a,button{font-family:'Ubuntu', sans-serif}.app{background-color:#292929;min-height:89vh}#game-item-ctn{background-color:#292929;display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));grid-template-rows:repeat(2, 1fr);padding:0, 20px;align-content:center;justify-items:center;min-height:89vh}h1,h2,h3,p,a,button{font-family:'Ubuntu', sans-serif}#cart-ctn{display:flex;flex-direction:column;align-items:center;justify-content:center;min-height:85vh;background-color:#292929}#cart-ctn #cart-item-ctn{display:flex;flex-direction:column;justify-content:space-around;align-items:center;background-color:white;width:50%;height:90%;background:linear-gradient(180deg, #ff7707 0%, #ff3c00 100%);box-shadow:0px 0px 15px 5px #000000}#cart-ctn #cart-item-ctn #total{margin-top:50px;font-size:2rem}#cart-ctn #cart-item-ctn #checkout-btn{background-color:white;border:2px solid black;border-radius:20px;text-align:center;font-size:2rem;text-decoration:none;color:black;padding-top:15px;width:200px;margin:20px auto;height:50px;cursor:pointer}#cart-text{margin:10px;padding:10px;text-align:center;font-size:4rem;border-bottom:#000 4px solid;width:100%;color:#000}.cart-item{box-shadow:0px 0px 3px 2px #000000;background-color:#fff;color:#000;display:flex;padding:10px;margin:5px;width:90%;height:40px;text-align:center;align-items:center;justify-content:space-around;font-size:1.2rem}.cart-item .amount-input{width:60px;margin-right:50px}.cart-item p{flex:2 1}.cart-item .remove-item-btn{flex:1 1;width:80px;font-size:1rem;height:40px;background-color:#020202;color:white;border:none}.cart-item .remove-item-btn:hover{background-color:#ad0202;color:white}#cart-button{width:70px;height:70px;margin-right:50px;fill:#ff5100}#cart-button:hover{fill:white}#cart-amount{color:white;text-decoration:none;list-style:none;position:absolute;top:20px;right:5px}#cart-btn-ctn{position:relative;display:flex;justify-content:center}.game-item{display:flex;flex-direction:column;text-align:center;justify-content:space-around;align-items:center;padding:10px;font-size:1.5rem;background-color:#fff458;border:3px solid #000;margin-top:20px;width:200px;max-width:90%;height:300px;cursor:pointer;background:linear-gradient(180deg, #ff7707 0%, #ff3c00 100%);transition:ease-out 0.2s;box-shadow:0px 0px 15px 5px #000000}.game-item:hover{transition:ease-in 0.2s;color:white;background:linear-gradient(180deg, #646464 0%, #000 100%)}.game-item img{max-width:100%}.checkout{text-align:center;display:flex;justify-content:space-around;align-items:center;font-size:4rem;height:84.2vh;color:white}.game-info{display:flex;position:absolute;top:200px;left:35%;height:50%;width:30%;align-items:center;justify-content:space-around;font-size:2rem;border-radius:20px;border:#000 3px solid;background:linear-gradient(180deg, #ff7707 0%, #ff3c00 100%);flex-direction:column;text-align:center}.game-info input{width:50px;font-size:1rem;height:20px}.overlay-disabled{display:none;position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(128,128,128,0.5)}.overlay-active{position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(42,41,41,0.87);display:block}.add-to-cart-btn{border:solid black 2px;background-color:white;font-size:1rem;width:100px;height:50px}.add-to-cart-btn:hover{background-color:black;color:white}nav{display:flex;align-items:center;justify-content:center;background-color:#000}#navbar{font-size:3rem;display:flex;justify-content:space-around;padding:20px;align-items:center;border-bottom:5px solid red}#navbar img{width:500px}.home-button{margin-right:350px;color:#fff;text-decoration:none}.home-button:hover{color:red}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}h1,h2,h3,p,a,button{font-family:'Ubuntu', sans-serif}

